import { createRouteHandlerClient } from '@supabase/auth-helpers-nextjs';
import { cookies } from 'next/headers';
import { NextResponse } from 'next/server';

export async function GET(request: Request) {
  try {
    // 1. Auth check
    const supabase = createRouteHandlerClient({ cookies });
    const { data: { session }, error: authError } = await supabase.auth.getSession();

    if (authError || !session) {
      return NextResponse.json(
        {
          success: false,
          error: { code: 'UNAUTHORIZED', message: 'Authentication required' },
        },
        { status: 401 }
      );
    }

    // 2. Parse query parameters
    const { searchParams } = new URL(request.url);
    const query = searchParams.get('q') || '';
    const typesParam = searchParams.get('types') || 'event,activity,content_page,accommodation';
    const types = typesParam.split(',').filter(Boolean);

    if (!query.trim()) {
      return NextResponse.json({
        success: true,
        data: {
          events: [],
          activities: [],
          content_pages: [],
          accommodations: [],
        },
      });
    }

    // 3. Search across entity types
    const results: {
      events: any[];
      activities: any[];
      content_pages: any[];
      accommodations: any[];
    } = {
      events: [],
      activities: [],
      content_pages: [],
      accommodations: [],
    };

    // Search events
    if (types.includes('event')) {
      const { data: events, error: eventsError } = await supabase
        .from('events')
        .select('id, title, date, location_id, locations(name)')
        .or(`title.ilike.%${query}%,description.ilike.%${query}%`)
        .is('deleted_at', null)
        .order('date', { ascending: true })
        .limit(10);

      if (!eventsError && events) {
        results.events = events.map((event: any) => ({
          id: event.id,
          name: event.title,
          date: event.date,
          location: event.locations?.name,
        }));
      }
    }

    // Search activities
    if (types.includes('activity')) {
      const { data: activities, error: activitiesError } = await supabase
        .from('activities')
        .select('id, title, date, capacity, location_id, locations(name)')
        .or(`title.ilike.%${query}%,description.ilike.%${query}%`)
        .is('deleted_at', null)
        .order('date', { ascending: true })
        .limit(10);

      if (!activitiesError && activities) {
        results.activities = activities.map((activity: any) => ({
          id: activity.id,
          name: activity.title,
          date: activity.date,
          capacity: activity.capacity,
          location: activity.locations?.name,
        }));
      }
    }

    // Search content pages
    if (types.includes('content_page')) {
      const { data: pages, error: pagesError } = await supabase
        .from('content_pages')
        .select('id, title, slug, type')
        .or(`title.ilike.%${query}%,slug.ilike.%${query}%`)
        .is('deleted_at', null)
        .eq('status', 'published')
        .order('title', { ascending: true })
        .limit(10);

      if (!pagesError && pages) {
        results.content_pages = pages.map((page: any) => ({
          id: page.id,
          title: page.title,
          slug: page.slug,
          type: page.type,
        }));
      }
    }

    // Search accommodations
    if (types.includes('accommodation')) {
      const { data: accommodations, error: accommodationsError } = await supabase
        .from('accommodations')
        .select('id, name, location_id, locations(name), room_types(count)')
        .or(`name.ilike.%${query}%,description.ilike.%${query}%`)
        .is('deleted_at', null)
        .order('name', { ascending: true })
        .limit(10);

      if (!accommodationsError && accommodations) {
        results.accommodations = accommodations.map((accommodation: any) => ({
          id: accommodation.id,
          name: accommodation.name,
          location: accommodation.locations?.name,
          room_count: accommodation.room_types?.[0]?.count || 0,
        }));
      }
    }

    return NextResponse.json({
      success: true,
      data: results,
    });
  } catch (error) {
    console.error('Reference search error:', error);
    return NextResponse.json(
      {
        success: false,
        error: {
          code: 'INTERNAL_ERROR',
          message: error instanceof Error ? error.message : 'Search failed',
        },
      },
      { status: 500 }
    );
  }
}
